import React from 'react';
import { Button } from '@vkontakte/vkui';
import './VkButton.sass';

const VkButton = (props) => {
  let style = {};

  if (props.side) {
    if (props.side === 'right') {
      style.marginLeft = 2.5;
    } else {
      style.marginRight = 2.5;
    }
  }

  return (
    <Button
    style={style}
    mode="commerce"
    size="xl"
    stretched
    onClick={props.handleClick}
    className={`${props.variant} ${props.scheme}`}
    id={props.id}
    data-to={props['data-to']}
    /* scheme={scheme === " " ? " " : ""}  */
    >
      {props.children}
    </Button>
  );
};

export default VkButton;